.projectsBlock {
   height: 100vh;
   background-color: #191919;
}

.projectsContainer {
   align-items: center;
   display: flex;
   flex-direction: column;
   flex-wrap: nowrap;
   justify-content: flex-start;
}

.title {
   color: rgba(246, 88, 0, 0.86);
}

.content {
   display: flex;
   flex-wrap: wrap;
   width: 100%;
   justify-content: space-around;
}

.projectContent {
   box-sizing: border-box;
   display: flex;
   flex-direction: column;
   justify-content: flex-start;
   margin-bottom: 25px;
   width: 40vh;
}

.image {
   width: 100%;
   height: 400px;
   background-color: rgba(246, 88, 0, 0.86);
}

.titleBlock {
   color: rgba(246, 88, 0, 0.86);
   text-align: center;
}

.descBlock {
   color: rgba(246, 88, 0, 0.86);
   text-align: center;
}


@media (max-width: 630px) {
   .projectsBlock {
      height: auto;
   }
}

