.skillsBlock {
    height: 100vh;
    background-color: #191919;
}

.skillsContainer {
   align-items: center;
   display: flex;
   flex-direction: column;
   flex-wrap: nowrap;
   justify-content: flex-start;
}

.title {
    color: rgba(246, 88, 0, 0.86);
    margin: 10px;
}

.skills {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

@media (max-width: 980px) {
   .skillsBlock {
      height: auto;
   }
}